body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    mono
    
}

#homePageContainer {
  display: flex;
  margin: auto;
  text-align: center;
  width: 75%;
  justify-content: space-between;
  
}

#showcase {
  margin-top: 25%;
}

#showCaseImgContainer {
  margin-top: 25%;
 
}

#showNowBtn {
  margin-top: 10%;
}

#product-list {
  display:flex;
  flex-wrap: wrap;
  text-align: center;
  margin: auto;
  width: 50%;
  margin-top: 3%;
}

#product-list div {
  padding: 20px;
  width: 23%;
  max-height: 100%;
  max-width: 100%;
}

#productSideBar {
    display: block;
    list-style-type: none;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;
    width: 10%;
    position: absolute;
  
}

#productSideBar li {
  margin-bottom: 5px;
  cursor: pointer;
}

#productListCategoryHeader {
  text-align: center;
  width: 50vw;
  margin-top: 5%;
  margin-left: 8vw;
  font-style:normal;
  font-size: 2vw; 
}


#product-Card:hover  #addToCartBtn{
  display: initial;
  border: 2px solid black; 
}

#addToCartBtn{
  border-radius: 4px;
  background-color: #e4000F;
  color: white;
  display: none;
}

#sortByHeader {
  margin-bottom: 30%;
}

#logo {
width: 200px;
}

#navbar {
  text-align: right;
  font-weight:600;
}

.navbar {
  background-color: #e4000F;
}
.navbar .ml-auto a{
  margin-left: 10px;
  margin-right: 10px;
  color: white !important;
}
.navbar .navbar-brand {
  color: #ecf0f1;
}
.nav{
  font-family: 'Roboto', sans-serif;
  color: white !important;
 
}

.navbar-expand {
  flex-wrap: nowrap;
}



.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000000dd;
  z-index: 9;
}



.cart {
  position: fixed;
  top: 0;
  right: 0;
  width: 33%;
  height: 100%;
 background-color: white;
  z-index: 10;
  overflow-y: scroll;
  list-style: none;
  
}

.cart #shoppingCartHeader {
  text-align: center;
  margin-bottom: 100px;
  
}


#shoppingCartHeader {
  text-align: center;
  margin-bottom: 20%;
  
}

#cartItem {
  padding: 1%;
}
#cartItem li{
  text-align: right; 
  
}
#cartItem img {
  float: left;
  width: 20%;
  
  
}
#removeButton {
  width: 3vw;
  float: left;
  font-size: .5vw;
  text-align: center;
}

#cartItem button{
  margin: 1%;
  border-radius: 4px;
  background-color: #e4000F;
  color: white;
}

#totalCartCostHeader {
  position: absolute;
  bottom: 3vw;
  left: 3vw;
  margin-top: 10%;
  font-size: 1.3em;
}

#checkOutBtn{
  position: absolute;
  bottom: 3vw;
  right: 3vw;
  border-radius: 4px;
  background-color: #e4000F;
  color: white;
  margin-top: 10%;
  margin-left: 30%;
  width: 100px;
  height: 40px;
  
  
 
}

#exitCartBtn{
  position: absolute;
  top: 0;
  right: 0;
  background-color: transparent;
  border: none;
  font-weight: bolder;
}



#productItemDisplay  {
 margin: auto;
 text-align: center;
 width: 50%;
 margin-top: 10%;
}


#productItemDisplay img {
  float: left;
}



#shopNowLink {
  border: solid 1px black;
  padding: 10px;
  background-color: #e4000F;
  color: white;
  font-weight: 500;
}

img {
  max-width: 100%;
  height: auto;
}

#product-list h5 {
  font-size: 1vw;
}


#productItemDisplay #addToCartBtn{
  border-radius: 4px;
  background-color: #e4000F;
  color: white;
  display: initial;

}

@media only screen and (max-width: 1000px) {
 
  #totalCartCostHeader {
    position: absolute;
    bottom: 3vw;
    left: 3vw;
    margin-top: 10%;
    font-size: 1.3em;
  }

  .cart {
    width: 50%;
  }

  #checkOutBtn {
    width: 100px;
    height: 40px;
  }

  #product-list {
    width: 100%;
  }

  #product-list h5 {
    font-size: 2vw;
   
  }

  #product-list #product-Card{
    width: 33%;
  }

  #productSideBar {
  display: inline;
  list-style-type: none;
  margin-block-start: initial;
  margin-block-end: initial;
  margin-inline-start: initial;
  margin-inline-end: initial;
  padding-inline-start: initial;
  position: initial;
}

#productSideBar li {
 display: inline;
 margin: 2%;
}

#sortByHeader {
  font-size: 4vw;
  margin-bottom: 2%;
  text-align: center;
}


#productListCategoryHeader {
    font-size: 4vw;
    text-align: left;
  }
}


@media screen and (max-width: 600px) 
{

  #homePageContainer {
    display: block;
  }

  #showCaseImgContainer {
    margin-bottom: 5%;
  }
  #product-list #product-Card {
    width:50%;
  }

  #navbar > nav > div
  {
   
    display: inline;
    margin: auto;
    margin-bottom: 1%;
  }

  .navbar form {
    margin: auto;
  }
  
  .navbar .form-control {
   
   width: 75%;
  }

  .navbar button {
    width: 25%;
  }

  .cart {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
   background-color: white;
    z-index: 10;
    overflow-y: scroll;
    list-style: none;
    
  }

  #totalCartCostHeader {
    position: absolute;
    bottom: 3vw;
    left: 3vw;
    margin-top: 10%;
    font-size: 1.3em;
  }

  #checkOutBtn {
    width: 100px;
    height: 40px;
  }
    
 
}
